import { Component, OnInit } from '@angular/core';
import { RegserviceService } from '../../servers/regservice.service';
import { Router, ActivatedRoute } from '@angular/router';
import { FilesService } from '../../servers/files.service';
import { saveAs } from 'file-saver';

@Component({
  selector: 'app-profile-view',
  templateUrl: './profile-view.component.html',
  styleUrls: ['./profile-view.component.css']
})
export class ProfileViewComponent implements OnInit {
  userDetails; 
  userId='';
  state;
  model ={
    newpassword :'',
    confirmnewPassword:'', 
    email:'',
  };

  constructor(private service: RegserviceService, private router: Router,private route: ActivatedRoute,private FileService: FilesService) { }
sub;
id;
  ngOnInit() {
    this.sub = this.route.params.subscribe(params => {
      this.id = params['id'];});
    this.service.getUser(this.id).subscribe(
      res => {console.log(res)
        this.userDetails = res['user'];
        this.userId=this.userDetails._id;
        this.service.setid(this.userDetails._id);
        this.model.email=this.userDetails.email
        // console.log(this.userId);
        // console.log(this.userDetails)
      },
      err => { 
        // console.log(err);
        
      });
  }
  private files = [];
  sent(){
this.state='Sent'
this.changepw=false
this.FileService.showFileNames(this.id).subscribe(response => {console.log(response)
  for (let i = 0; i < response.json().length; i++) {
    this.files[i] = {
      filename: response.json()[i].filename,
      originalname: response.json()[i].originalname,
      contentType: response.json()[i].mimetype,
      time:response.json()[i].time,
    };
    // console.log(response.json()[i].mimetype)
  }
});
  }
  uploads(){
    this.state='Admin Uploads'
    this.changepw=false
    this.FileService.recevefile(this.id).subscribe(response => {console.log(response)
      for (let i = 0; i < response.json().length; i++) {
        this.files[i] = {
          filename: response.json()[i].filename,
          originalname: response.json()[i].originalname,
          contentType: response.json()[i].mimetype,
          time:response.json()[i].time,
        };
        // console.log(response.json()[i].mimetype)
      }
    });
  }
  changepw=false
pw(){
this.changepw=true
this.state='Change password'
}
  downloadPdf(filename, contentType,originalname) {
    if(this.state=='Admin Uploads'){
    this.FileService.downloadPDF(filename, contentType).subscribe(
      (res) => {
        // console.log(res)
        const file = new Blob([res.blob()], { type: contentType });
        // console.log(file)
      const fileURL = URL.createObjectURL(file);
      // console.log(fileURL)
      // window.open(fileURL);
      saveAs(file, originalname);
      }
    );}
    else{
      this.FileService.downloaduserPDF(filename, contentType).subscribe(
        (res) => {
          // console.log(res)
          const file = new Blob([res.blob()], { type: contentType });
          // console.log(file)
        const fileURL = URL.createObjectURL(file);
        // console.log(fileURL)
        // window.open(fileURL);
        saveAs(file, originalname);
        }
      );
    }
  }
msg;
  onSubmit(){
    this.service.savepassword(this.model).subscribe(
      res => {
        // console.log(res);
        if(res['sucsess']==false){
          this.msg=res['message'];
          this.router.navigate(['login']);
        }else{
         this.model.newpassword ='';
         this.model.confirmnewPassword='';
          this.msg=res['message'];
this.changepw=false
        }
      },
      err => {
        // console.log(err);
        this.msg='';
      }
    );
  }
}
